// frontend/src/api/api.js 
 import axios from 'axios'; 
 const API = axios.create({ 
   baseURL: process.env.REACT_APP_API_URL || 'http://localhost:5000/api', 
   headers: { 
     'Content-Type': 'application/json', 
   }, 
 }); 

 // Interceptor para aadir el token JWT a cada solicitud saliente. 
 // Esto asegura que todas las peticiones a la API estn autenticadas 
 // si hay un token almacenado en localStorage. 
 API.interceptors.request.use((config) => { 
   const token = localStorage.getItem('token'); 
   if (token) { 
     // Aade el token en el formato "Bearer <token>" al header de Autorizacin. 
     config.headers.Authorization = `Bearer ${token}`; 
   } 
   return config; 
 }, (error) => { 
   // Si hay un error en la configuracin de la solicitud, se rechaza la promesa. 
   return Promise.reject(error); 
 }); 

 export default API;