// frontend/src/pages/HomePage.js
import React from 'react';
import { Link } from 'react-router-dom';
import '../index.css'; // Importa los estilos para esta pgina

const HomePage = () => {
    return (
        <div className="homepage-container">
            <h1 className="company-title">
                Mensajera <span className="highlight">Delivery Express S.A.S.</span>
            </h1>
            <p className="tagline">
                Tu confianza, nuestra prioridad. Entregamos ms que paquetes.
            </p>
            <div className="button-group">
                <Link to="/login" className="action-button primary-button">
                    Iniciar Sesin
                </Link>
                <Link to="/register" className="action-button secondary-button">
                    Registrarse
                </Link>
            </div>
            {/* Opcional: una imagen o ilustracin que transmita confianza y dinamismo */}
            <img
                src="https://via.placeholder.com/600x300?text=Logo+Delivery+Express" // Reemplaza con una imagen real ms adelante
                alt="Servicio de Mensajera"
                className="hero-image"
            />
        </div>
    );
};

export default HomePage;